//
// Material design icons
//
// Since icons are fonts, they can be placed anywhere text is placed and are
// thus automatically sized to match the surrounding child. To use, create an
// inline element with the appropriate classes, like so:
//
// <i class="material-icons">face</i>
//
// For browsers that do not support ligatures, fall back to specifying the icons
// using numeric character references like the example below:
//
// <i class="material-icons">&#xE87C;</i>

// We decided to load them from Google Fonts
// Import the fonts
//@font-face {
//  font-family: 'Material Icons';
//  font-style: normal;
//  font-weight: 400;
//  src: url('@{icon-font-path}@{icon-font-name}.eot'); /* For IE6-8 */
//  src: local('Material Icons'),
//       local('MaterialIcons-Regular'),
//       url('@{icon-font-path}@{icon-font-name}.eot?#iefix3ocs8m') format('embedded-opentype'),
//       url('@{icon-font-path}@{icon-font-name}.woff2') format('woff2'),
//       url('@{icon-font-path}@{icon-font-name}.woff') format('woff'),
//       url('@{icon-font-path}@{icon-font-name}.ttf') format('truetype');
//}

.material-icons {
  position: relative;
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 1.1em;
  display: inline-block;
  top: 2px;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  // text-rendering: auto;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

[class^="mdi-"], [class*="mdi-"] {
  &:before {
    display: inline-block;
    speak: none;
    text-decoration: inherit;
  }
}


